html {
  body {
    #root {
      #app {
        #main {
          #content-container {
            .barn {
              &:before {
                content: '';
                position: fixed;
                left: 0;
                right: 0;
                top: 0;
                width: 100%;
                height: 100vh;
                z-index: -1;
                -webkit-filter: opacity(0.5) blur(2px);
                -moz-filter: opacity(0.5) blur(2px);
                -o-filter: opacity(0.5) blur(2px);
                -ms-filter: bopacity(0.5) blur(2px);
                filter: opacity(0.5) blur(2px);
                background: url(../../assets/barnbg.png);
                background-position: top;
                background-size: cover;
              }

              .zoomed-item,
              .zoomed-pack {
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                align-items: flex-start;
                border: solid 1px var(--grey2);
                border-radius: 10px;

                background: radial-gradient(circle, rgba(107, 103, 64, 0.5) 0%, rgba(0, 0, 0, 1) 100%);
                width: 400px;
                height: 520px;
                overflow: hidden;

                .item-name {
                  text-transform: uppercase;
                  color: antiquewhite;
                  font-size: 1em !important;
                  font-family: monospace;
                }

                .item-description {
                  overflow-y: auto;
                  max-height: 65px;
                  color: antiquewhite;
                  font-size: 0.9em !important;
                  font-family: monospace;
                }

                .item-balance {
                  color: var(--orange);
                  font-family: monospace;
                  font-size: 0.9em !important;
                }

                .unrevealed {
                  align-self: center;
                  background: transparent;
                  height: 320px;
                }

                button {
                  align-self: center;
                }

                .modal-close {
                  -webkit-animation: text-focus-in 0.6s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
                  animation: text-focus-in 0.6s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
                  position: absolute;
                  right: 5px;
                  top: 5px;
                }

                -webkit-animation: scale-in-center 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
                animation: scale-in-center 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
              }

              .card {
                box-shadow: 0 4px 8px 2px rgba(0, 0, 0, 0.2), 0 6px 20px 4px rgba(0, 0, 0, 0.19);
                background-image: linear-gradient(rgba(10, 10, 10, 0.9), rgba(10, 10, 10, 0.8)),
                  url(../../assets/strawbg.png);
                background-position: top;
                background-size: 600px;

                background-size: 500%;

                padding: 15px;

                .empty-items {
                  color: antiquewhite;
                  font-family: monospace;
                }

                .buy-pack-button {
                  box-sizing: border-box;
                  margin-top: 7px;
                  background-repeat: no-repeat;
                  background-size: 450%;
                  background-position: top left;
                  height: 100px;
                  width: 100px;
                  background-color: rgba(0, 0, 0, 0.35) !important;
                  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                  border: solid 1px var(--bd-grey2);
                  border-radius: 10px;
                }

                .grid {
                  display: grid;
                  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
                  grid-gap: 2px;

                  .grid-item {
                    width: 100px;
                    height: 100px;

                    &.is-item {
                      position: relative;
                      height: 120px;

                      .is-item-useful {
                        position: absolute;
                        right: -10px;
                        bottom: -10px;
                      }
                      .is-health {
                        color: var(--green);
                      }
                      .is-morale {
                        color: var(--orange);
                      }
                    }

                    img {
                      height: 100px;
                    }
                    margin-bottom: 50px;
                    border: solid 1px #936d3b41;
                    border-radius: 10px;

                    background: radial-gradient(circle, rgba(36, 35, 35, 0.5) 0%, rgba(0, 0, 0, 1) 100%);

                    text-align: center;

                    &.unique {
                      background: radial-gradient(circle, #4b4b4375 0%, rgba(0, 0, 0, 1) 100%);
                    }
                    &.legendary {
                      background: radial-gradient(circle, #41413c60 0%, rgba(0, 0, 0, 1) 100%);
                    }
                    &.epic {
                      background: radial-gradient(circle, #33333145 0%, rgba(0, 0, 0, 1) 100%);
                    }
                    &.rare {
                      background: radial-gradient(circle, #29292730 0%, rgba(0, 0, 0, 1) 100%);
                    }

                    &:hover {
                      border: solid 1px var(--orange2);

                      .item-name {
                        font-size: 1em !important;
                        // color: var(--orange2);
                      }
                      .item-balance {
                        font-size: 1.1em !important;
                      }
                    }

                    .claim-items {
                      overflow-y: scroll;
                      height: 70px;
                    }

                    .claim-title {
                      text-transform: uppercase;
                      color: #6e6861;
                      font-size: 0.9em !important;
                      font-family: monospace;
                    }

                    .item-name,
                    .claim-name {
                      text-transform: uppercase;
                      color: antiquewhite;
                      font-size: 0.8em !important;
                      font-family: monospace;
                    }

                    .item-description {
                      text-transform: uppercase;
                      color: antiquewhite;
                      font-size: 0.7em !important;
                      font-family: monospace;
                    }

                    .rarity-display {
                      text-transform: uppercase;
                      color: #6e6861;
                      font-size: 0.7em !important;
                      font-family: monospace;
                    }

                    .item-balance,
                    .claim-amount {
                      color: var(--orange);
                      font-family: monospace;
                      font-size: 0.9em !important;
                    }

                    .pack-container {
                      height: 135px;

                      & > div {
                        margin-top: -30px;
                        margin-left: -30px;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
